
html {
  background-color: #f1f1f1;
  box-sizing: border-box;
  overflow-y: scroll;
}
*, *:before, *:after {
  box-sizing: inherit;
}
body {
  color: black;
  font-family: 'Roboto', sans-serif;
  font-weight: 300;
  padding: 32px;
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 2em;
}

/* Small devices (portrait tablets and large phones, 600px and up) */
@media only screen and (min-width: 600px) {
body {
    font-size: 20px;
}
}

/* Medium devices (landscape tablets, 768px and up) */
@media only screen and (min-width: 768px) {
body {
    font-size: 18px;
}
}

/* Large devices (laptops/desktops, 992px and up) */
@media only screen and (min-width: 985px) {
body {
    font-size: 24px;
}
}

/* Extra large devices (large laptops and desktops, 1200px and up) */
@media only screen and (min-width: 1200px) {
body {
    font-size: medium;
}
}
#app {
  margin: auto;
  max-width: 932px;
}
a {
  color: #888;
  text-decoration: none;
}
h1:not(:only-of-type) {
  font-size: 28px;
  font-weight: 500;
  text-align: center;
  margin-bottom: 0.25em;
  margin-top: 1em;
}
h1:only-of-type {
  font-size: 28px;
  font-weight: 500;
  text-align: center;
}
h2 {
  font-size: 18px;
  font-weight: 500;
  text-align: center;
}
h3 {
  font-size: 18px;
  text-align: center;
  margin-bottom: 0.5em;
  margin-top: 0.5em;
}
h4 {
  font-size: 14px;
  text-align: center;
  margin-bottom: 1em;
}
.button {
  background-color: #ddd;
  border-radius: 3px;
  padding: 8px 16px;
  font-size: 100%;
  transition: 0.3s;
}
.button:hover {
    background-color: #ccc;
    color: #666;
}
.lazy {
  position: relative;
  transition: 1s;
}
.lazy img {
    display: block;
    height: auto;
    opacity: 0;
    width: 100%;
}
.lazy.loading .spinner {
    opacity: 1;
}
.lazy.loaded {
    opacity: 1;
}
.lazy.loaded .spinner {
      opacity: 0;
      transition: none;
}
.lazy.loaded img {
      opacity: 1;
}
.spinner,
.spinner:after {
  border-radius: 50%;
  height: 40px;
  width: 40px;
}
.spinner {
  border: 4px solid rgba(0, 0, 0, 0.1);
  border-left: 4px solid #aaa;
  animation: spin 1.1s infinite linear;
  left: 50%;
  margin: -20px;
  position: absolute;
  top: 50%;
}
@keyframes spin {
0% {
    transform: rotate(0deg);
}
100% {
    transform: rotate(360deg);
}
}
.header {
  border-bottom: 1px solid #ddd;
  margin-bottom: 1em;
}
.headerUpper {
  border-bottom: 1px solid #ddd;
  margin-bottom: 1em;
  margin-left: 2em;
  margin-right: 2em;
}
.footer {
  -ms-flex-align: center;
      align-items: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  margin-top: 36px;
}
.footer span {
    color: #bbb;
    font-size: 14px;
    text-align: center;
}
.footer a {
    color: #bbb;
    font-size: 14px;
    text-align: center;
}
.urlLink {
  color: blue;
  -webkit-text-decoration-line: underline;
          text-decoration-line: underline;
}
@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
.ie11Hack {
    width: 100%;
}
}



